import io from './io'

export default {
    //  params: [mnemonic, className, gasLimit, contract, secondSecret]
    uploadDapp (params) {
        return io.post({
            api: 'dapp',
            method: 'upLoadDapp',
            params
        }) 
    },

    //  params: []
    getCreateDappFee (params) {
        return io.post({
            api: 'dapp',
            method: 'getCreateDappFee',
            params
        }) 
    },

    // params: [mnemonic, dappHash, fun, gasLimit,param, secondSecret]
    handleDapp (params) {
        return io.post({
            api: 'dapp',
            method: 'handleDapp',
            params
        })
    },

    // params: [dappHash]
    getDappInfo (params) {
        return io.post({
            api: 'dapp',
            method: 'getDappInfo',
            params
        })
    },

    //  params: [mnemonic,dappHash,transferAddress,secondSecret]
    transferDapp (params) {
        return io.post({
            api: 'dapp',
            method: 'transferDapp',
            params
        })
    },

    //dapp  params: [address, dappHash]
    searchDappBalance (params) {
        return io.post({
            api: 'dapp',
            method: 'searchDappBalance',
            params
        })
    },

    //dapp params: []
    searchDappList (params) {
        return io.post({
            api: 'dapp',
            method: 'searchDappList',
            params
        })
    },

    //hashdapp params: [dappHash]
    searchDappHash (params) {
        return io.post({
            api: 'dapp',
            method: 'searchDappHash',
            params
        })
    },

     //  params: [address,page, size]
    searchMineList (params) {
        return io.post({
            api: 'dapp',
            method: 'searchMineList',
            params
        })
    },

    //  params: [dappHash, transactionHash, address, page, size]
    searchDappHandle (params) {
        return io.post({
            api: 'dapp',
            method: 'searchDappHandle',
            params
        })
    },

    //
    transferDappFee (params = []) {
        return io.post({
            api: 'dapp',
            method: 'transferDappFee',
            params
        })
    },

    //
    getHandleDappFee (params = []) {
        return io.post({
            api: 'dapp',
            method: 'getHandleDappFee',
            params
        })
    }

}